<template>
  <div>
    <h1 class="mt-3">Schichten</h1>
    <table class="table">
      <thead>
        <tr>
          <th>Datum</th>
          <th>Dauer in h</th>
          <th>Ort</th>
          <th>Unternehmen</th>
          <th>Vorname</th>
          <th>Nachname</th>
        </tr>
      </thead>
      <tbody>
        <tr :key="index" v-for="(index,i) in 100">
            
          <td>
            {{ shifts[i].datum }}
          </td> 
          <td>
            {{ shifts[i].duration }}
          </td>
          <td>
            {{ventures[i].location }}
          </td> 
          <td> 
            {{ventures[i].name}}    
          </td>
          <td>
            {{ persons[i].firstname }}
          </td>
          <td>
            {{ persons[i].lastname }}
          </td>
        </tr>
      </tbody>
    </table>
    
  </div>
</template>

<script>
import axios from "axios";
export default {
  data: function () {
    return {
      shifts: [],
      persons: [],
      ventures: [],
    };
  },
  mounted: function() {
      this.getShifts();
      this.getPersons();
      this.getVentures();
  },
    
  methods: {
      getShifts: function() {
          let url = "http://localhost:8080/shiftplanner/shifts"
          axios.get(url).then(response => {
              this.shifts = response.data;
              this.shifts.sort((a,b) => new Date(a.datum).getTime() - new Date (b.datum).getTime());
          })
      },
      getPersons: function() {
          let url = "http://localhost:8080/shiftplanner/persons"
          axios.get(url).then(response => {
              this.persons = response.data;
          })
      },
      getVentures: function() {
          let url = "http://localhost:8080/shiftplanner/ventures"
          axios.get(url).then(response => {
              this.ventures = response.data;
          })
      },
  }
};
</script>